import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Slide from "@material-ui/core/Slide";
import { IconButton } from "@material-ui/core";
import TuneIcon from "@material-ui/icons/Tune";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControl from "@material-ui/core/FormControl";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="right" ref={ref} {...props} />;
});

export default function Filters({ timeFilter, variantFilter, updateTimeFilter, updateVariantFilter }) {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <div className="filters-btn">
        <IconButton onClick={handleClickOpen}>
          <TuneIcon color="primary" />
        </IconButton>
      </div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogContent>
          <div className="filters-list">
            <div className="filters-title">Время приготовления:</div>
            <FormControl component="fieldset">
              <RadioGroup
                aria-label="gender"
                name="time"
                value={timeFilter}
                onChange={updateTimeFilter}
              >
                <FormControlLabel
                  value="fast"
                  control={<Radio color="primary" />}
                  label="10-15 минут"
                />
                <FormControlLabel
                  value="normal"
                  control={<Radio color="primary" />}
                  label="15-30 минут"
                />
                <FormControlLabel
                  value="slow"
                  control={<Radio color="primary" />}
                  label="30 минут - час"
                />
                <FormControlLabel
                  value="all"
                  control={<Radio color="primary" />}
                  label="Все"
                />
              </RadioGroup>
            </FormControl>
            <div className="filters-title">Вариант:</div>
            <FormControl component="fieldset">
              <RadioGroup
                aria-label="gender"
                name="gender1"
                value={variantFilter}
                onChange={updateVariantFilter}
              >
                <FormControlLabel
                  value="breakfast"
                  control={<Radio color="primary" />}
                  label="Завтрак"
                />
                <FormControlLabel
                  value="lunch"
                  control={<Radio color="primary" />}
                  label="Обед"
                />
                <FormControlLabel
                  value="dinner"
                  control={<Radio color="primary" />}
                  label="Ужин"
                />
                <FormControlLabel
                  value="all"
                  control={<Radio color="primary" />}
                  label="Все"
                />
              </RadioGroup>
            </FormControl>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} variant="contained" color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
