import React from "react";
import { v4 as uuidv4 } from "uuid";
import { Link, useHistory, useLocation } from "react-router-dom";
import Filters from "./Filters";

const menuTypes = {
  breakfast: "завтрак",
  lunch: "обед",
  dinner: "ужин",
  all: "all",
};

const RecipesList = ({ data }) => {
  const history = useHistory();
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const variant = params.get('variant');
  const time = params.get('time');
  const [variantFilter, setVariantFilter] = React.useState(variant ? variant : "all");
  const [timeFilter, setTimeFilter] = React.useState(time ? time : "all");

  const updateVariantFilter = (event) => {
    setVariantFilter(event.target.value);
    history.push(`?time=${timeFilter}&variant=${event.target.value}`);
  };

  const updateTimeFilter = (event) => {
    setTimeFilter(event.target.value);
    history.push(`?time=${event.target.value}&variant=${variantFilter}`);
  };

  return (
    <>
      <Filters
        variantFilter={variantFilter}
        updateVariantFilter={updateVariantFilter}
        timeFilter={timeFilter}
        updateTimeFilter={updateTimeFilter}
      />
      {data
        .filter(({ type }) =>
          variantFilter === "all"
            ? true
            : type.toLowerCase() === menuTypes[variantFilter]
        )
        .filter(({ time }) =>
          timeFilter === "all" ? true : time.toLowerCase() === timeFilter
        )
        .map(
          ({
            id,
            type,
            title,
            videoType,
            videoPoster,
            videoSource,
            ingredients,
            description,
          }) => (
            <div className="menu-item" key={uuidv4()}>
              <small className="text-muted">{type}</small>
              <h2 className="m-0 p-0"><Link to={`/${id}`}>{title}</Link></h2>
              {videoSource ? (
                videoType === "youtube" ? (
                  <iframe
                    width="100%"
                    height="350"
                    title={uuidv4()}
                    src={videoSource}
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen
                  ></iframe>
                ) : (
                  <video
                    className="video"
                    width="100%"
                    height="350"
                    controls
                    poster={videoPoster}
                    type="video/mp4"
                    src={videoSource}
                  ></video>
                )
              ) : null}
              <br />
              <div className="lm-styled-list list-unstyled">
                <li>
                  <h3>Вам понадобится:</h3>
                </li>
                {ingredients.map(({ weight, product, dataW, dataR }, idx) => (
                  <li key={uuidv4()} data-w={dataW} data-r={dataR}>
                    <span className="number">{idx + 1}</span>
                    <nobr>{weight} гр.</nobr>
                    {product}
                  </li>
                ))}
              </div>
              <p
                className="small"
                dangerouslySetInnerHTML={{ __html: description }}
              />
              <hr></hr>
            </div>
          )
        )}
    </>
  );
};

export default RecipesList;
