import React from "react";
import { v4 as uuidv4 } from "uuid";
import { CircularProgress, IconButton } from "@material-ui/core";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { useHistory } from "react-router-dom";

const Recipe = ({
  recipeId,
  loading,
  data
}) => {
  const recipe = data.find(({ id }) => recipeId === id);
  const history = useHistory();
  
  const handleClickBack = () => {
      history.goBack();
  };

  if (loading || !recipe) {
    return (
      <div className="container" style={{ textAlign: "center" }}>
        <CircularProgress />
      </div>
    );
  }
  
  const {
    type,
    title,
    videoType,
    videoPoster,
    videoSource,
    ingredients,
    description,
  } = recipe;

  return (
    <>
      <div className="filters-btn">
        <IconButton onClick={handleClickBack}>
          <ArrowBackIcon color="primary" />
        </IconButton>
      </div>
      <div className="menu-item" key={uuidv4()}>
        <small className="text-muted">{type}</small>
        <h2 className="m-0 p-0">{title}</h2>
        {videoSource ? (
          videoType === "youtube" ? (
            <iframe
              width="100%"
              height="350"
              title={uuidv4()}
              src={videoSource}
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          ) : (
            <video
              className="video"
              width="100%"
              height="350"
              controls
              poster={videoPoster}
              type="video/mp4"
              src={videoSource}
            ></video>
          )
        ) : null}
        <br />
        <div className="lm-styled-list list-unstyled">
          <li>
            <h3>Вам понадобится:</h3>
          </li>
          {ingredients.map(({ weight, product, dataW, dataR }, idx) => (
            <li key={uuidv4()} data-w={dataW} data-r={dataR}>
              <span className="number">{idx + 1}</span>
              <nobr>{weight} гр.</nobr>
              {product}
            </li>
          ))}
        </div>
        <p
          className="small"
          dangerouslySetInnerHTML={{ __html: description }}
        />
        <hr></hr>
      </div>
    </>
  );
};

export default Recipe;
